// Syntax Settings
// --------------------------------------------
:root {
  --code-header-bg: $primary-color;
  --code-header-color: #fff;
  --code-bg: $bg-color;
}




.code-block {
  @include code;
  border-top: 24px solid var(--code-header-bg);
  margin: $space-one-half 0;
  position: relative;

  &:before {
    position: absolute;
    top: -23px;
    left: 10px;
    content: attr(data-code);
    color: var(--code-header-color);
    font-size: 0.75em;
    letter-spacing: 1px;
  }
}


// including code font
pre, code { @include code; }
pre, code { margin: 0; }




// Syntax Highlighting
// --------------------------------------------
.highlight {
  font-size: 0.7em;
  // background-color: $bg-color;
  background-color: var(--code-bg);
  // border-bottom: 2px solid #E4E4E4;
  padding: $space-one-half 20px $space-one-half 15px;
  margin: 0;
}


